import React, {useState} from 'react'
import 'bootstrap/dist/css/bootstrap.css';
import '../css/styles.css'
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import EmailIcon from '@mui/icons-material/Email';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';

function Footer(){

  const year = new Date().getFullYear()
  const date = new Date().toLocaleTimeString()
  const [time, setTime] = useState(date)
  setInterval(()=>{
    updateTime()
  }, 1000)
  function updateTime(){
    const current = new Date().toLocaleTimeString()
    setTime(current)
  }



  return (
    <div style={{backgroundColor: 'black', color: 'white'}}className="footer">
    <h2 class="f2">Contact Information</h2>
    <a class="icons" href="https://www.facebook.com/stephen.diehl2/"><FacebookIcon className="iconz" fontSize=''/></a>
    <a class="icons" href="https://twitter.com/StephenADiehl"><TwitterIcon className="iconz" fontSize=''/></a>
    <a class="icons" href="mailto: diehlstephen@yahoo.com"><EmailIcon className="iconz" fontSize=''/></a>
    <a class="icons" href="https://www.linkedin.com/in/stephen-a-diehl/"><LinkedInIcon className="iconz" fontSize=''/></a>
    <a class="icons" href="https://github.com/LightSCG"><GitHubIcon className="iconz" fontSize=''/></a>

    <h6> {time} </h6>
  <h6 class="boom"><span>Copyright Â©</span> Stephen Diehl {year}</h6>
    </div>
  )
}

export default Footer
